import { Icon } from '@iconify/react';
import { motion } from 'framer-motion';
import flashFill from '@iconify/icons-eva/flash-fill';
import { Link as RouterLink } from 'react-router-dom';
// material
import { styled, useTheme } from '@mui/material/styles';
import { Button, Box, Link, Container, Typography, Stack, useMediaQuery } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import {
  varFadeIn,
  varFadeInUp,
  varWrapEnter,
  varFadeInRight,
  TextAnimate,
  varFadeInLeft,
  varFadeInDown
} from '../../animate';

// ----------------------------------------------------------------------

const RootStyle = styled(motion.div)(({ theme }) => ({
  position: 'relative',
  backgroundColor: '#ffffff',
  [theme.breakpoints.up('md')]: {
    top: 0,
    left: 0,
    width: '100%',
    height: '100vh',
    display: 'flex',
    position: 'fixed',
    alignItems: 'center'
  }
}));

const ContentStyle = styled((props) => <Stack spacing={5} {...props} />)(({ theme }) => ({
  zIndex: 10,
  maxWidth: 520,
  margin: 'auto',
  textAlign: 'center',
  position: 'relative',
  paddingTop: theme.spacing(15),
  paddingBottom: theme.spacing(15),
  [theme.breakpoints.up('md')]: {
    margin: 'unset',
    textAlign: 'left'
  }
}));

const HeroImgStyle = styled(motion.img)(({ theme }) => ({
  top: 0,
  right: 0,
  bottom: 0,
  zIndex: 8,
  width: '100%',
  margin: 'auto',
  position: 'absolute',
  [theme.breakpoints.up('lg')]: {
    top: '15%',
    right: '10%',
    width: 'auto',
    height: '90vh'
  }
}));

const HeroImg2Style = styled(motion.img)(({ theme }) => ({
  zIndex: 8,
  width: '100%',
  margin: 'auto',
  [theme.breakpoints.up('lg')]: {
    width: 'auto',
    height: '98vh'
  }
}));

// ----------------------------------------------------------------------

export default function LandingHero() {
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('lg'));
  return (
    <>
      <RootStyle initial="initial" animate="animate" variants={varWrapEnter}>
        {isDesktop && (
          <HeroImgStyle
            alt="hero"
            src={`/static/home/${window.location.host !== 'lpcenterr.net' ? 'hero1' : 'hero'}.png`}
            variants={varFadeInRight}
          />
        )}

        <Container maxWidth="lg">
          <ContentStyle>
            {!isDesktop && (
              <HeroImg2Style
                alt="hero"
                src={`/static/home/${window.location.host !== 'lpcenterr.net' ? 'hero1' : 'hero'}.png`}
                variants={varFadeInDown}
              />
            )}
            <Typography variant="h1" sx={{ color: 'common.black' }}>
              <TextAnimate text="Commencer" variants={varFadeInDown} />
              <br />
              <TextAnimate text="votre" variants={varFadeInUp} />
              <TextAnimate text="&nbsp;loterie" variants={varFadeInUp} /> <br />
              <TextAnimate text="avec&nbsp;" variants={varFadeInUp} />
              <TextAnimate
                text={window.location.host !== 'lpcenterr.net' ? 'RLP Center' : 'LP Center'}
                sx={{ color: 'primary.main' }}
                variants={varFadeInUp}
              />
            </Typography>

            <motion.div variants={varFadeInRight}>
              <Typography variant="h5" sx={{ color: 'common.black' }}>
                Améliorez la gestion de votre entreprise de loterie grâce à notre plateforme tout-en-un.
              </Typography>
            </motion.div>
            <motion.div>
              <Button
                size="large"
                variant="contained"
                component={RouterLink}
                to={PATH_DASHBOARD.root}
                startIcon={<Icon icon={flashFill} width={20} height={20} />}
              >
                Connecter
              </Button>
            </motion.div>
          </ContentStyle>
        </Container>
      </RootStyle>
      <Box sx={{ height: { md: '100vh' } }} />
    </>
  );
}
